<template>
  <ul
    class="flex flex-wrap md:flex-nowrap border-solid border-b border-last hover:bg-gray-50 pt-2"
    :class="{ 'bg-gray-50': isShowAction }"
  >
    <li class="w-full md:w-5/12 mb-2 md:mb-0  flex justify-center">
      <div class="items-center pl-6 flex">
        <div class="w-10 mr-4">
          <img class="w-full" :src="pool.token0src" />
        </div>
        <div>
          <p class="text-xl font-bold">
            {{ pool.lpName }}
            <span
              v-if="pool.isNew"
              class="bg-green-400 text-white px-3 mx-auto text-sm shadow"
              >New</span
            >
          </p>
          <sapn class="text-sm bg-gray-100 text-gray-500 rounded px-1">
            1 {{ pool.lpName }} = 1 {{ pool.lpName.substring(2) }}
          </sapn>
        </div>
      </div>
    </li>
    <li class="w-5/12 md:w-3/12 flex justify-center items-center">
      <div class="flex flex-col text-center">
        <p class="text-lg">
          {{ formatBN(ibTotalSupply) }}
        </p>
        <p class="text-lg">{{ pool.lpName }}</p>
      </div>
    </li>
    <li class="w-5/12 md:w-3/12 flex justify-center items-center">
      <div class="flex flex-col text-center">
        <p class="text-lg">{{ formatBN(ibBalance) }} {{ pool.lpName }}</p>
        <p class="text-lg">
          {{ formatBN(wantBalance) }} {{ pool.lpName.substring(2) }}
        </p>
      </div>
    </li>
    <li class="w-2/12">
      <div
        class="flex flex-col text-center py-6 cursor-pointer"
        @click="isShowAction = !isShowAction"
      >
        <div
          class="cursor-not-allowed m-auto font-normal text-gray-500 relative  md:static -top-9"
        >
          <i v-show="isShowAction" class="icon ion-chevron-down"></i>
          <i v-show="!isShowAction" class="icon ion-chevron-right"></i>
        </div>
      </div>
    </li>
  </ul>
  <ul
    v-if="isShowAction"
    class="border-b-20 border-white text-gray-400"
    :class="{ 'bg-gray-50': isShowAction }"
  >
    <li colspan="4" class=" py-4">
      <div class="flex-col mx-6 md:flex md:flex-row justify-evenly">
        <div class="w-full md:w-5/12">
          <div class="text-sm text-gray-400">
            Available Balance: {{ formatBN(wantBalance) }}
            {{ pool.lpName.substring(2) }}
          </div>
          <div
            class="w-full flex items-center  border border-gray-200 bg-white"
          >
            <input
              v-model="depositAmt"
              type="number"
              placeholder="Deposit Amount"
              class="outline-none w-full h-10 rounded-md mt-1 pl-4"
            />
            <button
              @click="setDepositMax"
              class="focus:outline-none text-green-400 appearance-none outline-none h-4/5 border-l border-solid border-gray-200 w-20"
            >
              MAX
            </button>
          </div>
          <div
            class="mt-4 flex flex-wrap justify-center md:justify-between items-center"
          >
            <p class="text-sm">
              You will receive:
              <span class="text-green-400"
                >~ {{ depositAmt }} {{ pool.lpName }}</span
              >
            </p>
            <Button :loading="approving" @click="onApprove" v-if="needApprove">
              Approve
            </Button>
            <Button :loading="depositing" @click="onDeposit" v-else>
              Deposit
            </Button>
          </div>
        </div>
        <div class="my-4 border"></div>
        <div class="w-full md:w-5/12">
          <div class="text-sm text-gray-400">
            Available Balance: {{ formatBN(ibBalance) }} {{ pool.lpName }}
          </div>
          <div
            class="w-full flex items-center  border border-gray-200 bg-white"
          >
            <input
              v-model="withdrawAmt"
              type="number"
              placeholder="Withdraw Amount"
              class="outline-none w-full h-10 rounded-md mt-1 pl-4"
            />
            <button
              class="focus:outline-none text-green-400 appearance-none outline-none h-4/5 border-l border-solid border-gray-200 w-20"
            >
              MAX
            </button>
          </div>
          <div
            class="mt-4 flex flex-wrap justify-center md:justify-between items-center"
          >
            <p class="text-sm">
              You will receive:
              <span class="text-green-400"
                >~ {{ withdrawAmt }} {{ pool.lpName.substring(2) }}</span
              >
            </p>
            <Button :loading="withdrawing" @click="onWithdraw">
              Withdraw
            </Button>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
import { getContract, getProvider } from "@/utils/initWeb3";
import ibTokenABI from "@/assets/abi/ibToken.json";
import ERC20ABI from "@/assets/abi/ERC20.json";
import { ethers } from "ethers";
import { BigNumber } from "bignumber.js";
import Button from "./Button.vue";
export default {
  components: {
    Button,
  },
  props: {
    pool: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      isShowAction: false,
      needApprove: false,
      depositing: false,
      approving: false,
      withdrawing: false,
      ibTotalSupply: "0",
      ibBalance: "0",
      wantBalance: "0",
      depositAmt: "0",
      withdrawAmt: "0",
      ibToken: null,
      wantToken: null,
    };
  },
  watch: {
    "$store.state.account": async function(account) {
      if (account) {
        this.loadData();
      }
    },
  },
  async mounted() {
    if (this.$store.state.account) {
      this.loadData();
    }
  },
  methods: {
    async loadData() {
      this.ibToken = getContract(this.pool.lendContract, ibTokenABI);
      this.wantToken = getContract(await this.ibToken.want(), ERC20ABI);
      this.ibTotalSupply = await this.ibToken.totalSupply();
      this.needApprove =
        this.pool.lpName == "ibFTM"
          ? false
          : (
              await this.wantToken.allowance(
                this.$store.state.account,
                this.pool.lendContract
              )
            ).lt(ethers.utils.parseEther("10000000000"));
      this.wantBalance =
        this.pool.lpName == "ibFTM"
          ? await getProvider().getBalance(this.$store.state.account)
          : await this.wantToken.balanceOf(this.$store.state.account);
      this.ibBalance = await this.ibToken.balanceOf(this.$store.state.account);
    },
    setDepositMax() {
      this.depositAmt = this.formatBN(this.wantBalance, { dp: 18 });
    },
    setWithdrawMax() {
      this.withdrawAmt = this.formatBN(this.ibBalance, { dp: 18 });
    },
    async onDeposit() {
      try {
        this.depositing = true;
        const tx = await this.ibToken.deposit(
          ethers.utils.parseEther(this.depositAmt),
          {
            value:
              this.pool.lpName == "ibFTM"
                ? ethers.utils.parseEther(this.depositAmt)
                : 0,
          }
        );
        await tx.wait();
        this.load();
      } catch (error) {
        console.log("onDeposit", error);
      } finally {
        this.depositing = false;
      }
    },
    async onWithdraw() {
      try {
        this.withdrawing = true;
        const tx = await (new BigNumber(this.ibBalance).eq(
          ethers.utils.parseEther(this.withdrawAmt)
        )
          ? this.ibToken.withdrawAll()
          : this.ibToken.withdraw(ethers.utils.parseEther(this.withdrawAmt)));
        await tx.wait();
        this.load();
      } catch (error) {
        console.log("onDeposit", error);
      } finally {
        this.withdrawing = false;
      }
    },
    async onApprove() {
      try {
        this.approving = true;
        const tx = await this.wantToken.approve(
          this.pool.lendContract,
          ethers.BigNumber.from("2")
            .pow("256")
            .sub("1")
        );
        await tx.wait();
        this.needApprove = false;
      } catch (error) {
        console.log("onApprove", error);
      } finally {
        this.approving = false;
      }
    },
  },
};
</script>
